<template>
<div class="tooltip" v-if="getTooltips[this.name]">

  <div class="triangle">
  </div>
    {{message}}


</div>

</template>

<script>
import {mapActions, mapGetters} from "vuex";

export default {
  name: "PasswordRequireTooltip",
  props: {
    message: {
      type: String,
      required: true,
    },
    value1: {
      type: String,
      required: true,
    },
    value2: {
      type: String,
      required: true,
    },
    function: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true,
    }




  },
  watch: {
  value1: {
    deep: true,
    handler: function(){
      this.compare()
    },
  } ,
    value2: {
    deep: true,
      handler: function(){
        this.compare()
      }
    }
  },
  methods:{
    ...mapActions(['addNewTooltip']),
      compare(){
        if (this.function === 'checkPass'){
          let specialCharacter = /^((?=.*[0-9])(?=.*[а-я])(?=.*[А-Я]).{8,})|((?=.*[0-9])(?=.*[a-z])(?=.*[A-Z]).{8,})$/
          if (this.value1 === '') return  this.getTooltips[this.name] = false
          if (this.value1.search(specialCharacter) ){
           return this.getTooltips[this.name] = true
          }
          this.getTooltips[this.name] = false

        }
        if (this.function === 'compareTwoPass') {
          if (this.value2 === '' || this.value1 === '') return  this.getTooltips[this.name] = false
          if (this.value1 !== this.value2) {
            return this.getTooltips[this.name] = true
          }
          this.getTooltips[this.name] = false

        }


      }
  },
  mounted() {
    this.addNewTooltip(this.name)
  },
  computed: mapGetters(['getTooltips']),

}
</script>

<style scoped>
.tooltip {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #101828;
  padding: 2vh;
  width: 15vh;
  height: fit-content;
  color: white;
  border-radius: 1.5vh;
  font-family: Inter;
  font-weight: 500;
  font-size: 9px;
  line-height: 18px;
  /* or 164% */
  position: relative;
  letter-spacing: -0.01em;
}
.triangle {
  left: -0.8vh;
  width: 2vh;
  height: 2vh;
  position: absolute;
  border-radius: 0.3vh;
  transform: rotateZ(45deg);
  background: #101828;
}


</style>